.header {
     padding-top: 20px;
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        max-width: 172px;
    }
    &__block {
        display: flex;
        align-items: center;
        gap: 60px;
    }
    &__nav {
        display: flex;
        gap: 18px;
    }
    .nav__link {
        font-size: 18px;
        color: #171717;
        transition: color .2s ease-in-out;
        &:hover {
            color: #FF6600;
        }
    }
    &__contact {
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: flex-end;
    }
}

@media (max-width: 1400px) {
	.header {
        &__logo {
            max-width: 144px;
        }
        &__block {
            gap: 40px;
        }
    }
}

@media (max-width: 992px) {
	.header {
        &__block {
            display: none;
        }
        .contact__link-mail {
            display: none;
        }
        .burger {
            display: block;
        }
        .nav__link {
            font-size: 16px;
        }
    }
}

@media (max-width: 480px) {
    .header__contact {
        .contact__link {
            font-size: 15px;
        }
    }
}