* {
    font-family: "Muller", Helvetica, sans-serif;
}

.container {
    max-width: 1450px;
    width: 100%;
    margin: 0 auto;
    padding: 0 16px;
}

a {
    text-decoration: none;
    color: #171717;
}

.burger {
    display: none;
    position: relative;
    border: none;
    background: none;
    height: 20px;
    width: 30px;
    cursor: pointer;
    z-index: 100;
}

.burger-line {
    width: 30px;
    height: 2px;
    background-color: #FF6600;
    transition: 0.3s all ease;
}

.top-line {
    position: absolute;
    top: 0;
    right: 0;
}

.mid-line {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
}

.bottom-line {
    position: absolute;
    left: 0;
    bottom: 0;
}

.burger.active > .top-line {
    transform: translateY(8px) rotate(45deg);
    transition: 0.3s all ease;
}

.burger.active > .mid-line {
    opacity: 0;
    transition: 0.3s all ease;
}

.burger.active > .bottom-line {
    transform: translateY(-10px) rotate(-45deg);
    transition: 0.3s all ease;
}

.mobile-menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
    flex-direction: column;
    gap: 90px;
    z-index: 10;
}

.mobile__nav {
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;
}

.mobile__link {
    font-size: 20px;
    color: #171717;
    transition: color .2s ease-in-out;
    &:hover {
        color: #FF6600;
    }
}

.mobile__contact {
    flex-direction: row;
    gap: 20px;
    flex-wrap: wrap;
    .contact__link {
        font-size: 18px;
        color: #FF6600;
        &:hover {
            color: #171717 !important;
        }
    }
}

.mobile-menu.show {
    display: flex;
}

.footer {
    margin-top: 100px;
    &__content {
        font-weight: 300;
        font-size: 12px;
        line-height: 120%;
        text-align: center;
        color: #000000;
        margin: 0;
        padding: 0;
        padding: 20px 0 70px;
        a {
            opacity: 1;
            transition: opacity .2s ease-in-out;
            &:hover {
                opacity: .9;
            }
        }
    }
}


@media (max-width: 1680px) {
	.container {
		max-width: 1320px;
	}
}

@media (max-width: 1400px) {
	.container {
		max-width: 1140px;
	}
}

@media (max-width: 1170px) {
	.container {
		max-width: 960px;
	}
}

@media (max-width: 992px) {
	.container {
		max-width: 720px;
	}
    .footer {
        background: url("../img/footer-bg.png") bottom no-repeat;
        background-size: 100% 100%;
        hr {
            display: none;
        }
        &__content {
            font-size: 10px;
            color: #fff;
            padding: 30px 0 40px;
        }
        a {
            color: #fff;
            display: block;
        }
    }
}

@media (max-width: 768px) {
	.container {
		max-width: 540px;
    }
}

@media (max-width: 576px) {
	.container {
		max-width: 450px;
	}
}

@media (max-width: 480px) {
    .container {
		box-sizing: border-box;
	}
    .mobile__contact {
        flex-direction: column;
        gap: 10px;
    }
    .footer {
        &__content {
            font-size: 7px;
            padding: 30px 0 20px;
        }
    }
}