.hero {
    margin-top: 65px;
    background: url('../img/hero-bg.png') right no-repeat;
    background-position: 100% 0;
    &__breadcrumbs {
        font-weight: 400;
        font-size: 16px;
        color: 171717;
        opacity: 0.4;
        transition: opacity .2s ease-in-out;
        &:hover {
            opacity: .35;
        }
    }
    &__content {
        display: flex;
        justify-content: space-between;
        align-items:center;
    }
    &__block {
        max-width: 720px;
    }
    &__title {
        padding: 0;
        margin: 0;
        font-weight: 700;
        font-size: 27px;
        text-transform: uppercase;
        color: #171717;
    }
    &__descr {
        padding: 0;
        margin: 0;
        margin-top: 40px;
        font-weight: 400;
        font-size: 17px;
        color: #000000;
        line-height: 130%;
    }
    &__img {
        max-width: 578px;
    }
    &__mobile {
        display: none;
        align-items: center;
        gap: 40px;
        margin-bottom: 25px;
    }
}


@media (max-width: 1680px) {
	.hero {
        background-position: 110% 0;
    }
}

@media (max-width: 1400px) {
	.hero {
        background-position: 120% 0;
        &__img {
            max-width: 552px;
        }
    }
}

@media (max-width: 1170px) {
	.hero {
        background-position: 130% 0;
        &__img {
            max-width: 524px;
        }
        &__title {
            font-size: 24px;
        }
        &__descr {
            font-size: 16px;
        }
    }
}

@media (max-width: 992px) {
	.hero {
        overflow: hidden;
        background-position: 130% 0%;
        background-size: 550px;
        &__img.img__desktop {
            display: none;
        }
        &__title {
            margin-top: 10px;
            width: 100%;
        }
        &__breadcrumbs {
            font-size: 14px;
        }
        &__descr {
            margin-top: 25px;
            width: 100%;
        }
        &__mobile {
            display: flex;
        }
        &__img {
            max-width: 472px;
        }
    }
}

@media (max-width: 768px) {
	.hero {
        background-position: 140% 0%;
        background-size: 450px;
        &__title {
            font-size: 18px;
        }
        &__breadcrumbs {
            font-size: 12px;
        }
        &__descr {
            font-size: 14px;
        }
        &__img {
            max-width: 412px;
        }
        &__logo {
            max-width: 150px
        }
    }
}

@media (max-width: 576px) {
	.hero {
        background-position: 220% 0%;
        background-size: 400px;
        &__title {
            font-size: 16px;
        }
        &__descr {
            font-size: 12px;
        }
        &__img {
            max-width: 352px;
        }
        &__logo {
            max-width: 140px
        }
    }
}

@media (max-width: 480px) {
    .hero {
        background-position: 250% 0%;
        background-size: 320px;
        &__title {
            font-size: 14px;
        }
        &__breadcrumbs {
            font-size: 10px;
        }
        &__descr {
            margin-top: 15px;
            font-size: 10px;
        }
        &__img {
            max-width: 292px;
        }
        &__logo {
            max-width: 132px
        }
    }
}

@media (max-width: 380px) {
    .hero {
        background-position: 100% 0%;
        background-size: 130px 300px;
    }
}