.catalog {
    margin-top: 70px;
    &__item {
        margin-top: 100px;
        position: relative;
    }
    &__circle {
        background: url('../img/catalog-circle.png') top left no-repeat
    }
    &__content {
        position: relative;
        z-index: 10;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__icon {
        width: 26px;
        height: 26px;
    }
    &__title {
        margin: 0;
        padding: 0;
        margin-top: 14px;
        font-weight: 700;
        font-size: 27px;
        text-transform: uppercase;
        color: #171717;
        max-width: 300px;
    }
    // &__block:nth-child(1n) {
    //     margin-bottom: auto;
    // }
    &__nav {
        margin: 0;
        padding: 0;
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        gap: 12px;
    }
    &__link {
        list-style-type: none;
        font-weight: 300;
        font-size: 20px;
        a {
            color: #171717;
            transition: color .2s ease-in-out;
            &:hover {
                color: rgba(23, 23, 23, 0.4);
            }
        }
    }
    &__img {
        max-width: 612px;
        margin-right: 120px;
        margin-top: auto;
    }
    &__num {
        position: absolute;
        bottom: 0;
        left: 20%;
        font-weight: 700;
        font-size: 234px;
        line-height: 60%;
        color: rgba(217, 217, 217, 0.22);
    }
    &__wrap {
        position: absolute;
        bottom: 0;
        right: 60px;
        z-index: 1;
        span {
            color: #FFFFFF;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
}



@media (max-width: 1680px) {
	.catalog {
        &__img {
            margin-right: 100px;
            max-width: 452px;
        }
        &__num {
            font-size: 200px;
        }
        &__rectangle {
            width: 500px;
        }
        &__wrap {
            right: 60px;
        }
    }
}

@media (max-width: 1400px) {
	.catalog {
        &__img {
            margin-right: 50px;
            max-width: 412px;
        }
        &__num {
            font-size: 180px;
            left: 18%;
        }
        &__rectangle {
            width: 450px;
        }
        &__title {
            font-size: 24px;
        }
        &__nav {
            margin-top: 40px;
            gap: 10px;
        }
        &__link {
            font-size: 18px;
        }
        &__wrap {
            right: 60px;
            
        }
    }
}

